.fv-headBar{background-color:#ffffff;padding:15px 5px;margin-left:-20px;text-align:left;display:flex;align-items:center}.fv-headBar .fv-logo{width:50%;text-align:left;display:inline-flex;padding-left:25px;font-size:24px;line-height:1.2;align-items:center}.fv-headBar .fv-logo svg{width:50px;height:auto;margin-right:10px}.fv-headBar .fv-logo h1{font-size:24px;margin:0}.fv-headBar .fv-menu{width:50%;justify-content:flex-end;padding-right:10px;display:inline-flex}.fv-headBar .fv-menu a{cursor:pointer;font-size:14px;line-height:2;color:#0073aa;text-decoration:none;padding:2px 10px;background:white;border-color:#c8d7e1;border-style:solid;border-width:1px 0px 1px 1px}.fv-headBar .fv-menu a:last-child{border-right-width:1px}.fv-headBar .fv-menu .active{color:#fff;background:var(--color-primary);border-color:var(--color-primary)}.fv-headBar .fv-version{margin-left:10px;font-size:10px;background-color:#e8e8e8;padding:3px 6px;line-height:1.2;font-style:italic}
