.fv-sidebar{width:25%}.fv-sidebar .fv-sidebar-box{background:#FFF;margin-bottom:15px;margin-top:20px}.fv-sidebar .fv-sidebar-box h3{margin:0;padding:15px;border-bottom:1px solid #eee}.fv-sidebar .fv-sidebar-box .fv-sidebar-inner{padding:15px}.fv-sidebar .fv-sidebar-box .fv-sidebar-inner ul{margin-top:0}.fv-sidebar .fv-sidebar-box .fv-sidebar-inner a{text-decoration:none}.fv-sidebar .fv-sidebar-box .fv-sidebar-inner.fv-licensing-box{padding-top:13px}.fv-sidebar .fv-sidebar-box .fv-sidebar-inner.fv-licensing-box .fv-license-key{display:flex;flex-direction:column}.fv-sidebar .fv-sidebar-box .fv-sidebar-inner.fv-licensing-box .fv-license-key .fv-license-key-input{width:100%;box-sizing:border-box;-webkit-transition:all 0.30s ease-in-out;-moz-transition:all 0.30s ease-in-out;-ms-transition:all 0.30s ease-in-out;-o-transition:all 0.30s ease-in-out;outline:none;box-shadow:0 0 5px #51cbee;border:1px solid #51cbee}.fv-sidebar .fv-sidebar-box .fv-sidebar-inner.fv-licensing-box .fv-license-key .fv-license-key-status{font-weight:normal;font-size:80%}.fv-sidebar .fv-sidebar-box .fv-sidebar-inner.fv-licensing-box .fv-license-key .fv-license-key-status-success{color:green}.fv-sidebar .fv-sidebar-box .fv-sidebar-inner.fv-licensing-box .fv-license-key .fv-license-key-status-fail{color:red}.fv-sidebar .fv-sidebar-box .fv-sidebar-inner.fv-licensing-box .fv-button-primary{margin-top:6px;position:relative}.fv-sidebar .fv-sidebar-box .fv-sidebar-inner.fv-licensing-box .fv_deactivate{background:#ccc;border-color:#ccc}
